import React from 'react'
import styles from './stringHandle.module.sass'
  
function StringHandle() {
  const BookData = () => {
    const booksData = ',,79284,,http://www.books.com.tw/img/001/076/22/0010762208.jpg;,,79284,2021,http://www.books.com.tw/img/001/076/22/0010762208.jpg;,,79308,,https://media.taaze.tw/showLargeImage.html?sc=11100810285&width=289&height=386'
    console.log('booksData',booksData)
  
    const booksSplitList = booksData.split(';')
    const booksDetailSplitList = booksSplitList.map((cur) => {
      const arr = cur.split(',')
      return arr
    })

    const data = booksDetailSplitList.map((cur) => {
      const discountData = cur[2].split('')
      return {
        bookName: cur[0],
        author: cur[1],
        discount: discountData[0],
        price: Math.ceil(Number(discountData[1].split('')[0]) / (Number(discountData[0] / 100))),
        description: cur[3],
        imgUrl: cur[4],
      }
    })

    console.log(data)

    return (
      <div className={styles.bookList}>
        {data.map((cur, index) => (
          <div key={index} className={styles.book}>
            <img src={cur.imgUrl} alt="" />
            <h3>{cur.author}</h3>
            <h1>{cur.bookName}</h1>
            <h2>{`${cur.price}(${cur.discount})`}</h2>
            <p>{`${cur.description.slice(0, 50)}...`}</p>
          </div>
        ))}
      </div>   
    )
  }

  //   (.*?) ()   .  *  ?
  // `4<H2></H2>5`.match(/<h2>(.*?)<\/h2>/)
  // = ["<h2></h2>", ""]
  const HtmlData = () => {
    const htmlData = `
    <ul>
      <li>
        <h3></h3>
        <p>
          <span>2017/11/12</span>
        </p>
      </li>
      <li>
        <h3></h3>
        <p>
          <span>2017/10/15</span>
        </p>
      </li>
    </ul>
  `
    console.log('htmlData',htmlData)
    const dataReplace = htmlData.replace(/\n/g, '') // 
    const memberMatch = dataReplace.match(/<li>(.*?)<\/li>/g)
    const memberList = memberMatch.map((cur) => {
      return {
        name: cur.match(/<h3>(.*?)<\/h3>/)[1].trim(),
        date: cur.match(/<span>(.*?)<\/span>/)[1].trim(),
        content: cur.match(/<p>(.*?)<\/p>/)[1].split('<span>')[0].trim(),
      }
    })
    console.log('memberList',memberList)

    return (
      <div className={styles.memberList}>
        {memberList.map((cur, index) => (
          <div key={index} className={styles.member}>
            <div>{`${cur.name}`}</div>
            <div>{`${cur.date}`}</div>
            <div>{`${cur.content}`}</div>
          </div>
        ))}
      </div>
    )
  }

  return (
    <div className={styles.stringHandle}>
      <h1 className={styles.title}>  </h1>
      <BookData />
      <h1 className={styles.title}>html  ()</h1>
      <HtmlData />
    </div> 
  )
}
          
export default StringHandle